<template>
    <div>
        <h3>意见反馈</h3>
        <textarea type="text" class="opinion" placeholder="输入不得少于20字！" autofocus v-model="texts"></textarea>
        <button @click="goBack" class="fl">返回</button> <button @click="submit">提交</button> <button @click="clear" class="fr">清空</button>
    </div>
</template>
<script>
export default {
    data(){
        return{
            texts:"",
        }
    },
    methods:{
        clear(){
            this.texts = ""
        },
        submit(){
            if(this.texts.length>20){
               alert('提交成功！')
               this.texts = ""
            }else{
                if(this.texts===""){
                    this.texts = "输入不得为空！"
                }else{
                    this.texts = "输入字数过少！" 
                }
            }
            
        },
        goBack(){
            this.$router.go(-1)
        }
    }
}
</script>
<style lang="scss" scoped>
div{
    position: fixed;
    top: 40px;
    width: 100%;
    height: 100%;
    background-color: #fff; 
    
    h3{
        padding-top: 5px;
        font-weight: bold;
        text-align: center;
    }
    .opinion{
        margin: 7.5%;
        padding: 10px;
        width: 80%;
        height: 40%;    
    }

    button{
        float: right;
        margin: 10px 30px 0 0;
    }

    .fl{
        float: left;
        margin-left: 30px;
    }

    .fr{
        margin: 10px;
    }
}
</style>